	module.exports = {"data":{"markdownRemark":{"html":"<p>上课，讨论西蒙的《管理决策新科学》和《管理行为》，以及弗洛姆的《逃避自由》。弗洛姆的《逃避自由》是一本很有深度的书。我觉得中国陷入一种二律背反：他的理论是说在市场经济的发展过程中，在现代个人主义文化的弘扬过程中，个人获得了自由，但是却孤独了。这样个人就有可能逃避自由。而中国的情况是在东方文化中，逃避自由是来自于集体主义价值。于是，如果中国的市场经济进一步发展，最后导致个人的自由领域的扩大，但是最后可能不是一种更加多的民主，而是弗洛姆所说的\"逃避自由\"。那么中国社会的发展趋向是什么呢?</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0068.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0068.md","frontmatter":{"title":"4月5日 星期二"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0068.md absPath of file >>> MarkdownRemark"}}

